import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			couponValidList: [],
			headerTop: 0,
			//控制滑动效果
			typeClass: 'valid',
			subState: '',
			theIndex: null,
			oldIndex: null,
			isStop: false,
			//加载loading标记
			refreshFlag: true,
			loadingStatus:'loading',
			noDataFlag:false,	//没有数据的标志
		};
	},
	onLoad() {
		this.getUserCoupon();
	},
	onPullDownRefresh() {
		this.couponValidList=[];
		this.getUserCoupon();
	},
	methods: {
		//查看优惠卷的信息
		detail(couponId) {
			console.log(couponId);
			uni.navigateTo({
				url: '/pages/shop/shop_collection_coupon/shop_collection_coupon?couponId=' + couponId
			});
		},
		dealData(data) {
			for (var i = 0; i < data.length; i++) {
				data[i]['coupon_price'] = data[i]['coupon_price'].substring(0, data[i]['coupon_price'].indexOf('.'));
				console.log(data[i]);
			}
			return data;
		},
		getUserCoupon() {
			let that = this;
			that.basePost(
				that.U({ c: 'coupons_api', a: 'get_use_coupon' }),
				{},
				function(res) { 
					uni.stopPullDownRefresh();
					if(res.data.length==0){
						that.noDataFlag=true;
					}
					that.refreshFlag=false;
					that.couponValidList = that.dealData(res.data); 
					
				},
				function(res) {
					console.log(res);
				}
			);
		},
		switchType(type) {
			if (this.typeClass == type) {
				return;
			}
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 0
			});
			this.typeClass = type;
			this.subState = this.typeClass == '' ? '' : 'show' + type;
			setTimeout(() => {
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass == 'valid' ? '' : this.subState;
			}, 200);
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150);
				}
			}
		},

		touchEnd(index, $event) {
			//解除禁止触发状态
			this.isStop = false;
		},

		//删除商品
		deleteCoupon(id, List) {
			let len = List.length;
			for (let i = 0; i < len; i++) {
				if (id == List[i].id) {
					List.splice(i, 1);
					break;
				}
			}
			this.oldIndex = null;
			this.theIndex = null;
		},

		discard() {
			//丢弃
		}
	}
};
